import React, { useState, useEffect } from 'react';
import { auth, firestore } from '../firebase';
import { doc, getDoc, setDoc, collection, query, where, getDocs } from 'firebase/firestore';
import { useParams, Link, useNavigate } from 'react-router-dom';

function UserProfile() {
  const { customURL } = useParams();
  const [user, setUser] = useState(null);
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [photoURL, setPhotoURL] = useState(null);
  const [previousPhotoRef, setPreviousPhotoRef] = useState(null);
  const [message, setMessage] = useState({ text: '', type: '' });
  const [imageLoadError, setImageLoadError] = useState(false);
  const [address, setAddress] = useState('');
  const [city, setCity] = useState('');
  const [county, setCounty] = useState('');
  const [postcode, setPostcode] = useState('');
  const [mobileNumber, setMobileNumber] = useState('');
  const [email, setEmail] = useState('');
  const [role, setRole] = useState('');
  const navigate = useNavigate();

  const handleMessageClick = () => {
    const userProfileQuery = query(
      collection(firestore, 'userProfiles'),
      where('customURL', '==', customURL)
    );
  
    getDocs(userProfileQuery)
      .then((userProfileQuerySnapshot) => {
        if (!userProfileQuerySnapshot.empty) {
          const otherUserUID = userProfileQuerySnapshot.docs[0].id;
          const currentUserUID = auth.currentUser.uid;

          console.log('Current User UID:', currentUserUID);
        console.log('Other User UID:', otherUserUID);
  
          // Navigate to the chat page and pass the UIDs as state
          navigate('/chat', {
            state: {
              currentUserUID,
              otherUserUID,
            },
          });
        } else {
          console.log('Custom URL not found:', customURL);
        }
      })
      .catch((error) => {
        console.error('Error fetching data:', error);
        setMessage({ text: 'Error accessing chat', type: 'error' });
      });
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        if (!customURL) {
          console.log('Custom URL is not defined.');
          return;
        }

        const userProfileQuery = query(
          collection(firestore, 'userProfiles'),
          where('customURL', '==', customURL)
        );
        const userProfileQuerySnapshot = await getDocs(userProfileQuery);

        if (!userProfileQuerySnapshot.empty) {
          const userData = userProfileQuerySnapshot.docs[0].data();
          setFirstName(userData.displayName.split(' ')[0] || '');
          setLastName(userData.displayName.split(' ')[1] || '');
          setAddress(userData.address || '');
          setCity(userData.city || '');
          setCounty(userData.county || '');
          setPostcode(userData.postcode || '');
          setMobileNumber(userData.mobileNumber || '');
          setRole(userData.role || '');
          setEmail(userData.email || '');
          setPhotoURL(userData.photoURL || null);
          const otherUserUID = userProfileQuerySnapshot.docs[0].id;
          console.log('Other User UID:', otherUserUID);

        } else {
          console.log('Custom URL not found:', customURL);
        }
      } catch (error) {
        console.error('Error fetching data:', error);
      }
    };

    if (customURL) {
      fetchData();
    }
  }, [customURL]);

  const handleConnectButtonClick = async () => {
    try {
      const authUser = auth.currentUser;
      if (!authUser) {
        throw new Error('User is not authenticated');
      }
  
      let userDocument;
  
      // Check if the authenticated user is in userProfiles or expertApplications
      const userProfilesCollection = collection(firestore, 'userProfiles');
      const expertApplicationsCollection = collection(firestore, 'expertApplications');
      const userProfileDoc = doc(userProfilesCollection, authUser.uid);
      const expertApplicationDoc = doc(expertApplicationsCollection, authUser.uid);
  
      const userProfileSnapshot = await getDoc(userProfileDoc);
      const expertApplicationSnapshot = await getDoc(expertApplicationDoc);
  
      if (userProfileSnapshot.exists()) {
        userDocument = userProfileDoc;
      } else if (expertApplicationSnapshot.exists()) {
        userDocument = expertApplicationDoc;
      } else {
        throw new Error('User document not found in either userProfiles or expertApplications');
      }
  
      const userDocRef = doc(firestore, userDocument.path);
      const userDocSnap = await getDoc(userDocRef);
  
      if (userDocSnap.exists()) {
        const userData = userDocSnap.data();
        const contacts = userData.contacts || {};
  
        // Add the new contact to the map
        contacts[otherUserUID] = {}; // Store an empty object or any relevant data instead of a boolean
  
        // Update the user document with the new contacts map
        await setDoc(userDocRef, {
          ...userData,
          contacts,
        }, { merge: true });
      }
  
      // You might want to show a success message or update the UI here
    } catch (error) {
      console.error('Error adding contact:', error.message);
    }
  };



  return (
<div className="profile-container">
<div className="profile-intro-section">
<div className="container">

</div>
</div>
<div className="profile-section">
<div className="content profile-content ">
<div className="row">
<div className="card-profile">
</div>
<div className="row g-3">
<div className="card-body">
    <div className="d-flex">
        {!imageLoadError && photoURL && (
          <div>
            <img
              src={photoURL}
              alt="User Profile"
              className="user-avatar large-avatar mb-3"

            />
           <div className="px-3">
              <div className="card-title h5">{`${firstName} ${lastName}`}</div>
              <div className="text-muted  card-subtitle h6">{role}</div>
            </div>
          </div>
        )}

        {imageLoadError && (
          <div>
            <img
              src={`${process.env.PUBLIC_URL}/images/arts/user.webp`}
              alt="Custom 404 Image"
              className="user-avatar large-avatar rounded-circle mb-3"

            />
            <div className="px-3">
              <div className="card-title">{`${firstName} ${lastName}`}</div>
              <div className="fw-normal card-subtitle mb-4 h6">{role}</div>
            </div>
          </div>
        )}
      </div>

 

    </div>
    <div className="d-flex responsive-buttons">
    <Link to="/chat">
        <button
          type="button"
          className="me-4 btn btn-primary btn-sm rounded-pill pt-2 pb-2"
          onClick={handleMessageClick}
        >
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" width="25" className="me-2 svg-inline--fa ">
            <path fillRule="evenodd" d="M4.804 21.644A6.707 6.707 0 006 21.75a6.721 6.721 0 003.583-1.029c.774.182 1.584.279 2.417.279 5.322 0 9.75-3.97 9.75-9 0-5.03-4.428-9-9.75-9s-9.75 3.97-9.75 9c0 2.409 1.025 4.587 2.674 6.192.232.226.277.428.254.543a3.73 3.73 0 01-.814 1.686.75.75 0 00.44 1.223zM8.25 10.875a1.125 1.125 0 100 2.25 1.125 1.125 0 000-2.25zM10.875 12a1.125 1.125 0 112.25 0 1.125 1.125 0 01-2.25 0zm4.875-1.125a1.125 1.125 0 100 2.25 1.125 1.125 0 000-2.25z" clipRule="evenodd" />
          </svg>
          Message
        </button>
    </Link>
<button   onClick={handleConnectButtonClick} type="button" className="btn btn-secondary btn-sm rounded-pill pt-2 pb-2">
<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" width="25" className="me-2 svg-inline--fa ">
<path d="M6.25 6.375a4.125 4.125 0 118.25 0 4.125 4.125 0 01-8.25 0zM3.25 19.125a7.125 7.125 0 0114.25 0v.003l-.001.119a.75.75 0 01-.363.63 13.067 13.067 0 01-6.761 1.873c-2.472 0-4.786-.684-6.76-1.873a.75.75 0 01-.364-.63l-.001-.122zM19.75 7.5a.75.75 0 00-1.5 0v2.25H16a.75.75 0 000 1.5h2.25v2.25a.75.75 0 001.5 0v-2.25H22a.75.75 0 000-1.5h-2.25V7.5z" />
</svg>
Connect
</button>
</div>
</div>
            <div className="my-4">
            <h3>Profile of <span> </span>
              {`${firstName} ${lastName}`} 
              </h3>
            </div>
            <form className="file-upload">
              <div className="row mb-4 gx-4">
                <div className="col-xl-8 col-12">
                  <div className="">
                    <div className="card-body card-profile">
                      <div className="row g-3">
                        <h4 className="mb-4">General information</h4>
                        <div className="col-md-6">
                          <label className="form-label">First Name</label>
                          <input
                            type="text"
                            className="form-control form-control-lg"
                            placeholder=""
                            aria-label="First name"
                            value={firstName}
                            readOnly // Make it read-only
                            style={{ pointerEvents: 'none' }}
                          />
                        </div>
                        <div className="col-md-6">
                          <label className="form-label">Last Name </label>
                          <input
                            type="text"
                            className="form-control form-control-lg"
                            placeholder=""
                            aria-label="Last name"
                            value={lastName}
                            readOnly // Make it read-only
                            style={{ pointerEvents: 'none' }}
                          />
                        </div>
                        <div className="col-md-6">
                          <label htmlFor="inputEmail4" className="form-label">Email </label>
                          <input
                            type="email"
                            className="form-control form-control-lg"
                            id="inputEmail4"
                            value={user ? user.email : ''}
                            readOnly // Make it read-only
                            style={{ pointerEvents: 'none' }}
                          />
                        </div>
                        <div className="col-md-6">
                          <label className="form-label">Mobile number </label>
                          <input
                            id="inputMobileNumber"
                            type="text"
                            className="form-control form-control-lg"
                            placeholder=""
                            aria-label="Phone number"
                            value={mobileNumber}
                            readOnly // Make it read-only
                            style={{ pointerEvents: 'none' }}
                          />
                        </div>
                        <div className="col-md-6">
<label className="form-label">Role/Expertise </label>
<input
id="inputRole"
type="text"
className="form-control form-control-lg"
placeholder=""
aria-label="your Role"
value={role} // Display user's mobile number here
onChange={(e) => setRole(e.target.value)}
readOnly // Make it read-only
style={{ pointerEvents: 'none' }}
/>
</div>
                      </div>
                      <div className="py-5 rounded mt-4">
                        <div className="row g-3">
                          <h4 className="mb-4 mt-0">Address</h4>
                          <div className="col-md-6">
                            <label className="form-label">Address</label>
                            <input
                              id="inputAddress"
                              type="text"
                              className="form-control form-control-lg"
                              placeholder=""
                              value={address}
                              readOnly // Make it read-only
                              style={{ pointerEvents: 'none' }}
                            />
                          </div>
                          <div className="col-md-6">
                            <label className="form-label">City</label>
                            <input
                              id="inputCity"
                              type="text"
                              className="form-control form-control-lg"
                              placeholder=""
                              value={city}
                              readOnly // Make it read-only
                              style={{ pointerEvents: 'none' }}
                            />
                          </div>
                          <div className="col-md-6">
                            <label className="form-label">County</label>
                            <input
                              id="inputCounty"
                              type="text"
                              className="form-control form-control-lg"
                              placeholder=""
                              value={county}
                              readOnly // Make it read-only
                              style={{ pointerEvents: 'none' }}
                            />
                          </div>
                          <div className="col-md-6">
                            <label className="form-label">Postcode</label>
                            <input
                              id="inputPostcode"
                              type="text"
                              className="form-control form-control-lg"
                              placeholder=""
                              value={postcode}
                              readOnly // Make it read-only
                              style={{ pointerEvents: 'none' }}
                            />
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>


</div>



</form>
</div>
</div>
</div>
</div>
);
}

export default UserProfile;
